<template>
    <div>
      <el-dialog
      width="640px"
      :title="(isCreate ? '新增' : '编辑') + '司机信息'"
      :visible.sync="isVisible"
      :close-on-click-modal="false"
      :close-on-press-escape="false"
      @close="cancel"
      >
        <el-form ref="driverForm" :model="driverForm" :rules="formRules" label-width="110px">
           <el-row>
               <el-col :span="12">
                  <el-form-item label="所属机构" prop="drvOrgUuid">
                    <el-select v-model="driverForm.drvOrgUuid" @change="getLineList(driverForm.drvOrgUuid)">
                      <el-option
                        v-for="item in lineOrgs"
                        :key="item.orgUuid"
                        :label="item.orgName"
                        :value="item.orgUuid">
                      </el-option>
                    </el-select>
                  </el-form-item>
                </el-col>
                 <el-col :span="12">
                    <el-form-item label="所属线路" prop="drvLineUuid">
                        <el-select v-model="driverForm.drvLineUuid">
                        <el-option
                            v-for="item in lines"
                            :key="item.lineUuid"
                            :label="item.lineName"
                            :value="item.lineUuid">
                        </el-option>
                        </el-select>
                    </el-form-item>
                </el-col>
            </el-row>
             <el-row>
              <el-col :span="12">
                 <el-form-item label="司机姓名" prop="drvName">
                      <el-input v-model="driverForm.drvName" placeholder="请输入司机姓名" />
                  </el-form-item>
                </el-col>
              <el-col :span="12">
                 <el-form-item label="员工编号" prop="drvEmployeeId">
                      <el-input v-model="driverForm.drvEmployeeId" placeholder="请输入员工编号" />
                 </el-form-item>
               </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                 <el-form-item label="驾驶证编号" prop="drvCardNum">
                      <el-input v-model="driverForm.drvCardNum" placeholder="请输入司机驾驶证编号" />
                  </el-form-item>
                </el-col>
              <el-col :span="12">
                 <el-form-item label="IC卡号" prop="drvIcCard">
                      <el-input v-model="driverForm.drvIcCard" placeholder="请输入IC卡号" />
                 </el-form-item>
               </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                 <el-form-item label="司机电话" prop="drvMobilePhone">
                      <el-input v-model.number="driverForm.drvMobilePhone" placeholder="请输入司机电话" />
                  </el-form-item>
                </el-col>
              <el-col :span="12">
                 <el-form-item label="紧急联系人电话" prop="drvEmergeContactPhone">
                      <el-input v-model.number="driverForm.drvEmergeContactPhone" placeholder="请输入紧急联系人电话" />
                 </el-form-item>
               </el-col>
            </el-row>
            <el-row>
              <el-col :span="12">
                 <el-form-item label="司机电话" prop="drvSex">
                      <el-radio-group v-model="driverForm.drvSex">
                        <el-radio label="1">男</el-radio>
                        <el-radio label="0">女</el-radio>
                     </el-radio-group>
                  </el-form-item>
                </el-col>
            </el-row>
            <el-row>
                <el-col :span="6" :offset="18">
                    <el-button type="primary" @click="save">保存</el-button>
                    <el-button @click="cancel">取消</el-button>
                </el-col>
            </el-row>
       </el-form>
      </el-dialog>
    </div>
</template>
<script>
// api
import { getAllOrgList, linesList } from '@/api/common'
import { createDriver, updateDriver } from '@/api/management'

import isvalidPhone from '@/utils/extendValidate/extendValidate'
const initDriversForm = {
  drvOrgUuid: '', // 组织结构id
  drvLineUuid: '', // 线路id
  drvName: '', // 司机姓名
  drvEmployeeId: '', // 员工编号
  drvCardNum: '', // 驾驶证编号
  drvIcCard: '', // IC卡号
  drvMobilePhone: '', // 电话
  drvEmergeContactPhone: '', // 紧急联系人电话
  drvSex: '1' // 性别

}
export default {
  name: 'BusUpdate',
  data () {
    return {
      lineOrgs: [],
      lines: [],
      isVisible: false,
      driverForm: Object.assign({}, initDriversForm),
      formRules: {
        drvOrgUuid: [{required: true, message: '请选择所属机构', trigger: 'change'}],
        drvLineUuid: [{required: true, message: '请选择线路', trigger: 'change'}],
        drvName: [{required: true, message: '请输入司机姓名', trigger: 'blur'}],
        drvEmployeeId: [{required: true, message: '请输入员工编号', trigger: 'blur'}],
        drvMobilePhone: [{ validator: isvalidPhone.cellPhone, message: '手机号应为11位数字', trigger: 'blur' }],
        drvEmergeContactPhone: [{ validator: isvalidPhone.cellPhone, message: '手机号应为11位数字', trigger: 'blur' }]
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    roleData: {
      type: Object
    },
    isCreate: {
      type: Boolean,
      required: true
    }
  },
  watch: {
    show (isShow) {
      if (isShow) {
        if (this.isCreate) {
          this.driverForm = Object.assign({}, initDriversForm)
        } else {
          this.driverForm = Object.assign({}, this.roleData)
        }
      }
      this.isVisible = isShow
    }
  },
  mounted () {
    getAllOrgList().then(res => {
      this.lineOrgs = res
    })
    const params = {lineOrgUuid: '', lineIsvalid: '1', lineDropFlag: 0}
    linesList(params).then(res => {
      this.lines = res
    })
  },
  methods: {
    // 通过组织机构的id和启用，是否删除来获取当前线路
    getLineList (lineOrgUuid) {
      this.driverForm.drvLineUuid = ''
      this.lines = []
      const params = {lineOrgUuid: lineOrgUuid, lineIsvalid: '1', lineDropFlag: 0}
      linesList(params).then(res => {
        this.lines = res
      })
    },
    save () {
      this.$refs.driverForm.validate(isValid => {
        if (isValid) {
          const params = Object.assign({}, this.driverForm)
          const submitType = this.isCreate ? createDriver : updateDriver
          const submitText = this.isCreate ? '新增' : '编辑'
          submitType(params).then(res => {
            this.$message({
              message: `${submitText}司机成功!`,
              type: 'success'
            })
            this.cancel()
            this.$emit('updateList')
          })
        }
      })
    },
    cancel () {
      this.driverForm = Object.assign({}, initDriversForm)
      this.$refs.driverForm.resetFields()
      this.$emit('update:show', false)
      this.isVisible = false
    }
  }
}
</script>



