<template>
     <el-form  :model="searchForm" ref="searchForm"  label-width="80px" >
        <el-row>
           <el-col :span="5">
             <el-form-item label="机构" prop="drvOrgUuid">
                <el-select v-model="searchForm.drvOrgUuid" placeholder="请选择" @change="getLineList(searchForm.drvOrgUuid)">
                  <el-option
                    v-for="item in lineOrgs"
                    :key="item.orgUuid"
                    :label="item.orgName"
                    :value="item.orgUuid">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="5">
              <el-form-item label="线路" prop="drvLineUuid">
                <el-select v-model="searchForm.drvLineUuid" placeholder="请选择">
                  <el-option
                    v-for="item in lines"
                    :key="item.lineUuid"
                    :label="item.lineName"
                    :value="item.lineUuid">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
            <el-col :span="4">
              <el-form-item label="员工姓名" prop="drvName">
                <el-input v-model="searchForm.drvName" placeholder="请输入员工姓名"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="4">
              <el-form-item label="员工编号" prop="drvEmployeeId">
                  <el-input v-model="searchForm.drvEmployeeId" placeholder="请输入员工编号"></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="6" class="flex-right">
              <el-form-item>
                  <el-button type="primary" @click.native="search">查询</el-button>
                  <el-button @click="resetForm('searchForm')">重置</el-button>
              </el-form-item>
            </el-col>
         </el-row>
      </el-form>
</template>

<script>
// api
import { getAllOrgList, linesList } from '@/api/common'
export default {
  name: 'DriverManagementSearch',
  data () {
    return {
      lineOrgs: [], // 机构列表
      lines: [], // 线路列表
      searchForm: {
        drvOrgUuid: '',
        drvLineUuid: '',
        drvName: '',
        drvEmployeeId: ''

      }
    }
  },
  mounted () {
    getAllOrgList().then(res => {
      this.lineOrgs = res
    })
    const params = {lineOrgUuid: '', lineIsvalid: '1', lineDropFlag: 0}
    linesList(params).then(res => {
      this.lines = res
    })
    this.search()
  },
  methods: {
    // 通过组织机构的id和启用，是否删除来获取当前线路
    getLineList (lineOrgUuid) {
      this.searchForm.drvLineUuid = ''
      this.lines = []
      const params = {lineOrgUuid: lineOrgUuid, lineIsvalid: '1', lineDropFlag: 0}
      linesList(params).then(res => {
        this.lines = res
      })
    },
    // 检索
    search: function () {
      this.$emit('search', Object.assign({}, this.searchForm))
    },
    // 重置
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.$emit('rest', Object.assign({}, this.searchForm))
    }
  }

}
</script>
<style lang="scss" scoped>
.el-form-item{
   margin-bottom:10px;
}
</style>



